import React from 'react'
import useStyles from './styles';
import { guestLogin, authLogin } from '../../actions/auth';
import { useDispatch } from 'react-redux';
  
const LoginContainer = () => {

  const dispatch = useDispatch();
  const classes= useStyles();

  return (
      <div style={{height: '90vh',display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
        <div className={classes.container}>
            <div className={classes.pinContainer}>
                <p>Login with PIN:</p>
                <input type="text" onChange={(e)=>{if(e.target.value=="6812"){dispatch(authLogin)}}}/>
            </div>
            <p className={classes.guestButton} onClick={()=>dispatch(guestLogin)}>Login as Guest</p>
        </div>
      </div>
  )
}

export default LoginContainer