import React from 'react';
import useStyles from './styles';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import CardMedia from '@mui/material/CardMedia';
import ThumbUpAltIcon from '@mui/icons-material/ThumbUpAlt';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import AutoStories from '@mui/icons-material/AutoStories';
import moment from 'moment';
import { useDispatch } from 'react-redux';
import { deletePost, likePost } from '../../../actions/posts'; 
import { Link } from "react-router-dom";

const Post = ({ post, setCurrentId, isAuth }) => {
    const classes = useStyles();
    const dispatch = useDispatch();
    console.log(isAuth )

    return (
        <>
            <Card className={classes.card}>
                <CardMedia className={classes.media} image={post.selectedFile} title={post.title} />
                <div className={classes.overlay}>
                    <Typography variant="h6">{post.creator}</Typography>
                    <Typography variant="body2">{moment(post.createdAt).fromNow()}</Typography>
                </div>
                {isAuth!=false?(
                    <div className={classes.overlay2}>
                        <Button style={{ color: 'white' }} size="small" onClick={() => { window.scrollTo(0, 0); setCurrentId(post._id) }}>
                            <EditIcon />
                        </Button>
                    </div>
                ):null}
                <div className={classes.details}>
                    <Typography variant="body2" color="textSecondary">Tags: {post.tags.map((tag) => `${tag} `)}</Typography>
                </div>
                <Typography className={classes.title} variant='h5' gutterBottom>{post.title} </Typography>
                <CardContent>
                    <Typography variant='p' gutterBottom ><p className={classes.message} dangerouslySetInnerHTML={{__html: post.message}}></p> </Typography>
                </CardContent>
                <CardActions className={classes.cardActions}>
                    {/* <Link to={`/read/${post._id}`} style={{textDecoration: 'none'}}> */}
                    <Link to={`${process.env.PUBLIC_URL}/read/${post._id}`} style={{textDecoration: 'none'}}>
                        <Button size="small" color="primary">
                            <AutoStories fontSize='small' />
                            &nbsp; Read
                        </Button>
                    </Link>
                    <Button size="small" color="primary" onClick={() => dispatch(likePost(post._id))}>
                        <ThumbUpAltIcon fontSize='small' />
                        &nbsp; Like {post.likeCount}
                    </Button>
                    <Button size="small" color="primary" onClick={() => dispatch(deletePost(post._id))}>
                        <DeleteIcon fontSize='small' />
                        Delete
                    </Button>
                </CardActions>
            </Card>
        </>
    );
}

export default Post;