import React, { useEffect } from 'react'
import { useParams } from "react-router-dom";
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import { getPosts } from '../../actions/posts';
import { CircularProgress } from '@mui/material';
import useStyles from './styles';
import { Container, AppBar, Typography, Grow, Grid } from '@material-ui/core';
import memories from '../../images/memories.png';
import { Link } from "react-router-dom";
import ArrowBack from '@mui/icons-material/ArrowBack';

const ReadPost = () => {
    let { postId } = useParams();
  const dispatch = useDispatch();
  const classes= useStyles();
  window.scrollTo(0, 0);

    const posts = useSelector((state)=>state.posts);
    let currPost=[]

    currPost = posts.filter((post)=>{
        return post._id == postId
    })

    if(currPost.length==0){
      currPost=[{
        "tags": [
          "cricket, university, captain"
        ],
        "likeCount": 1,
        "createdAt": "2022-03-08T17:40:20.385Z",
        "_id": "62279af3b0c4220023b7a0c0",
        "title": "First match after a long time",
        "message": "After not playing matches, coming back, as a captain felt really good. University tournament scored 200 in 20 overs and got them all out at 31. Took 3 wickets and gave one run in 2 overs. Felt really good to play a match after so long.",
        "selectedFile": "data:image/jpeg;base64,",
        "creator": "Rohit Parakh",
        "__v": 0
      }]
    }

    console.log(JSON.stringify(currPost[0]));

    useEffect(() => {
        dispatch(getPosts());
      }, [dispatch]);

  return (
    <>
    <Container maxWidth="lg">
   <AppBar className={classes.appBar} position="static" color="inherit">
    {/* <Link style={{textDecoration: 'none'}} to="/"> */}
    <Link style={{textDecoration: 'none'}} to={`${process.env.PUBLIC_URL}/`}>
    <ArrowBack style={{marginLeft: '20px'}}/>
    </Link>
    {/* <Link className={classes.appBar} style={{textDecoration: 'none', flex:'2', justifyContent: 'center'}} to="/"> */}
    <Link className={classes.appBar} style={{textDecoration: 'none', flex:'2', justifyContent: 'center'}} to={`${process.env.PUBLIC_URL}/`}>
        <Typography className={classes.heading} variant="h2" align="center">Memories</Typography>
        <img className={classes.image} src={memories} alt="icon" height="60" />
      </Link>
      </AppBar>
      <Grow in>
        <Container maxWidth='lg'>
          <Grid container justify="space-between" alignItems="stretch" spacing={3}>
            <Grid item xs={12} sm={12}>
              {!currPost.length? <CircularProgress/> : (<> 
                <img className={classes.imageContainer} style={{maxWidth: '100%', marginBottom: '15px'}} src={currPost[0].selectedFile}/>
                <div className={classes.textContainer}>
                  <Typography className={classes.title} variant='h3' gutterBottom>{currPost[0]?.title} </Typography>
                  <div className={classes.message} dangerouslySetInnerHTML={{ __html: currPost[0]?.message }}></div>
                </div>
                </>)}
            </Grid>
          </Grid>
        </Container>
      </Grow>
    </Container>
      
    </>
  )
}

export default ReadPost