import { GUEST_LOGIN, AUTH_LOGIN, LOGOUT } from '../constants/actionTypes';

const initialState = {
    isAuth: false,
    isLogged: false
}

export default (state=initialState, action) => {
  switch (action.type) {
    case GUEST_LOGIN:
        return {
            isAuth: false,
            isLogged: true
        }

    case AUTH_LOGIN:
        return {
            isAuth: true,
            isLogged: true
        }   
    case LOGOUT:
        return {
            isAuth: false,
            isLogged: false
        }
    default:
      return state;
  }
};

