import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware, compose } from 'redux';
import thunk from 'redux-thunk';
import { BrowserRouter, Routes, Route } from "react-router-dom";

import { reducers } from './reducers';
import App from './App';
import './index.css';
import ReadPost from './components/ReadPost/ReadPost';

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducers, /* preloadedState, */ composeEnhancers(applyMiddleware(thunk)));
// alert(process.env.PUBLIC_URL)
ReactDOM.render(
  <Provider store={store}>
    <BrowserRouter>
      <Routes>
        <Route exact path={`${process.env.PUBLIC_URL}/`} element={<App />} />
        <Route exact path={`${process.env.PUBLIC_URL}/read/:postId`} element={<ReadPost />} />
        <Route
          element={
            < App/>
          }
        />  
      </Routes>
    </BrowserRouter>
  </Provider>,
  document.getElementById('root'),
);
